@charset "UTF-8";
.category-container {
  width: 67.5%;
  margin: 0 auto;
  padding: 0 12px; }

.category {
  display: flex;
  justify-content: center; }

.category-item-focus {
  border-bottom: 8px solid #AA0601; }

.category-item {
  width: 33.3%;
  padding: 16px 0; }

.category-item:hover {
  border-bottom: 8px solid #AA0601; }

/* 太陽眼鏡BN */
.sunglasses-banner {
  display: flex;
  height: 296px; }

.sunglasses-banner-img-1 {
  width: 50%;
  background-image: url(../images/product-header-3.png);
  background-size: cover;
  background-position: center; }

.sunglasses-banner-img-2 {
  width: 50%;
  background-image: url(../images/product-header-4.png);
  background-size: cover;
  background-position: center; }

/* 太陽眼鏡BN END*/
/* 商品目錄 */
.newCollection {
  display: flex;
  flex-wrap: wrap; }

.newCollection-item {
  width: 30%; }

.newCollection-item a:hover {
  filter: brightness(80%); }

.newCollection-content {
  display: flex;
  justify-content: space-between; }

.sunglasses-color {
  display: flex;
  justify-content: start; }

.sunglasses-color-1 {
  display: inline-block;
  width: 24px;
  height: 24px;
  background-color: #5F3E2D;
  border-radius: 4px; }

.sunglasses-color-2 {
  display: inline-block;
  left: 0;
  width: 24px;
  height: 24px;
  background-color: #B75929;
  border-radius: 4px; }

/* 商品目錄END */
@media (max-width: 992px) {
  .category-container {
    width: 100%; }
  .category-item {
    width: 33.3%;
    padding: 16px 0; }
  .category-item a {
    font-size: 24px;
    font-weight: 700; }
  /* 太陽眼鏡BN */
  .sunglasses-banner {
    margin-bottom: 80px; }
  .sunglasses-banner-img-1 {
    background-image: url(../images/product-header-sm-3.png); }
  .sunglasses-banner-img-2 {
    width: 50%;
    background-image: url(../images/product-header-sm-4.png);
    background-size: cover;
    background-position: center; }
  /* 太陽眼鏡BN END */
  /* 商品目錄 */
  .newCollection-container {
    width: 100%; }
  .newCollection-container h2 {
    font-size: 42px; }
  .newCollection-container h3 {
    font-size: 32px;
    margin-bottom: 32px; }
  .newCollection {
    flex-wrap: wrap;
    justify-content: center;
    margin-bottom: 80px;
    padding: 0 36px; }
  .newCollection-item {
    width: 43.2%;
    font-size: 24px;
    margin-right: 0;
    margin-bottom: 24px; }
  .newCollection-item-mr {
    margin-right: 24px; }
  /* 商品目錄END */ }

@media (max-width: 767px) {
  .category-container {
    margin-bottom: 0px; }
  .category {
    justify-content: space-between; }
  .category-item a {
    font-size: 16px;
    font-weight: 700; }
  /* 太陽眼鏡BN */
  .sunglasses-banner {
    height: 136px;
    margin-bottom: 40px; }
  .sunglasses-banner-img-1 {
    background-image: url(../images/product-header-sm-3.png); }
  .sunglasses-banner-img-2 {
    width: 50%;
    background-image: url(../images/product-header-sm-4.png);
    background-size: cover;
    background-position: center; }
  /* 太陽眼鏡BN ENd */
  /* 商品目錄 */
  .newCollection-container {
    width: 100%;
    padding: 0 13px; }
  .newCollection-container h2 {
    font-size: 24px; }
  .newCollection-container h3 {
    font-size: 24px; }
  .newCollection {
    flex-wrap: wrap;
    margin-bottom: 64px; }
  .newCollection-item {
    width: 100%;
    font-size: 24px;
    margin-right: 0;
    margin-bottom: 24px; }
  /* 商品目錄END */ }
